<template>
  <div>
    <div class="logo" @click="restart">NORK</div>
    <div class="centerNotTop">
      <div class="title">"{{Result_Data[0]}}"</div>
      <div class="img" v-if="Result_Data[0] == Result_Type.디지털원주민"><img src="@/assets/img/result_0.png"></div>
      <div class="img" v-if="Result_Data[0] == Result_Type.디지털학도"><img src="@/assets/img/result_1.png"></div>
      <div class="img" v-if="Result_Data[0] == Result_Type.디지털이방인"><img src="@/assets/img/result_2.png"></div>
      <div class="des" v-html="Result_Data[1]"></div>
      <div class="point">당신의 특징은?</div>
      <ul class="lists">
        <li class="list" v-for="(e, idx) in Result_Data[2]" :key="idx">{{e}}</li>
      </ul>
      <div class="btns">
        <button class="result-share" @click="resultShare">결과 공유하기</button>
        <button class="restart" @click="restart">다시하기</button>
        <button class="test-share" @click="testShare">테스트 공유하기</button>
      </div>
    </div>
  </div>
</template>

<script>
import { Result_Data, Result_Type, Go_Start, copyMessage, Base_Folder, ClipBoard_Change, ClipBoard_Text} from '@/store'
const copyClipboard = (text) => {
  copyMessage();
  navigator.clipboard.writeText(text);
}

export default {
  name: 'Result',
  components: {
  },
  data() {
    return {
      Result_Type: Result_Type
    }
  },
  computed: {
    result_Score_Type() {
      return this.$store.state.result_Score_Type;
    },
    Result_Data() {
      let result = null;
      if(this.result_Score_Type === "디지털 원주민") {
        result = Result_Data[0];
      } else if(this.result_Score_Type === "디지털 학도") {
        result = Result_Data[1];
      } else if(this.result_Score_Type === "디지털 이방인") {
        result = Result_Data[2];
      }
      if(!result) {
        result = [,,[]];
      }
      return result;
    }
  },
  methods: {
    resultShare() {
      let url = `${window.location.hostname}${Base_Folder}/`;
      if(this.result_Score_Type === "디지털 원주민") {
        url += '디지털원주민';
      } else if(this.result_Score_Type === "디지털 학도") {
        url += '디지털학도';
      } else if(this.result_Score_Type === "디지털 이방인") {
        url += '디지털이방인';
      }
      this.$store.commit(ClipBoard_Change, ClipBoard_Text[1]);
      copyClipboard(url);
    },
    restart() {
       this.$store.commit(Go_Start);
    },
    testShare() {
      let url = `${window.location.hostname}${Base_Folder}`;
      this.$store.commit(ClipBoard_Change, ClipBoard_Text[0]);
      copyClipboard(url);
    }
  }
}
</script>

<style scoped>

  .alert_copy {
    position: fixed;
    background-color: #36C98E;
    width: 100vw;
    height: 50px;
    text-align: center;
  }

  .alert_copy_text {
    color: white;
    font-size: 15px;
    margin: 0 auto;
    line-height: 50px;
  }

  .centerNotTop {
    text-align: center;
    margin: 90px auto 0;
  }

  .title {
    font-size: 40px;
    font-weight: 900;
    text-shadow: 6px 6px 10px #00000029;
  }

  .img {
    margin-top: 30px;
  }

  .des {
    width: 580px;
    font-size: 18px;
    font-weight: 500;
    margin: 60px auto 0;
    text-align: left;
  }

  .point {
    width: 580px;
    margin: 140px auto 30px;
    text-align: left;
    font-size: 24px;
    font-weight: 900;
  }

  ul {
    width: 580px;
    text-align: left;
    margin: 0 auto;
    list-style: none;
  }

  ul li {
    margin-top: 6px;
  }

  ul li::before {
    content: "●";
    color: #0A59FA;
    font-weight: bold;
    font-size: 10px;
    display: inline-block; 
    width: 10px;
    vertical-align: middle;
    padding: 0px 10px 6px 0px;
  }

  .btns {
    margin-top: 192px;
    margin-bottom: 173px;
  }

  button {
    width: 180px;
    height: 62px;
    background-color: #0A59FA;
    box-shadow: 6px 6px 10px #00000029;
    border-radius: 5px;
    border: none;
    font-size: 21px;
    font-weight: 500;
    color: #FFFFFF;
    cursor: pointer;
  }

  .restart {
    background-color: #0A59FA;
    margin-left: 20px;
    margin-right: 20px;
  }

  @media screen and (max-width: 728px) { 
  
    .centerNotTop {
      margin-top: 50px;
    }

    .title {
      font-size: 22px;
    }

    .img {
      margin: 30px auto 0;
      width: 90vw;
    }

    .img img {
      width: 100%;
    }

    .des {
      width: 70vw;
      font-size: 16px;
      font-weight: 500;
      margin: 30px auto 0;
      text-align: left;
    }

    .point {
      width: 70vw;
      margin: 60px auto 10px;
      font-size: 16px;
    }

    ul {
      width: 70vw;
      text-align: left;
      margin: 0 auto;
      list-style: none;
    }

    ul li {
      margin-top: 6px;
      font-size: 16px;
    }

    ul li::before {
      content: "●";
      color: #0A59FA;
      font-weight: bold;
      font-size: 10px;
      display: inline-block; 
      width: 10px;
      vertical-align: middle;
      padding: 0px 10px 6px 0px;
    }

    .btns {
      margin-top: 60px;
      margin-bottom: 173px;
    }

    .restart {
      margin: 0 auto 20px;
    }

    button {
      display: block;
      font-size: 16px;
      margin: 0 auto 20px;
    }
  }
</style>