<template>
  <div>
    <div class="logo" @click="logo">NORK</div>
    <div class="page_container">
      <div class="center">
      <div class="page">
        <div class="cur_Page">{{cur_Question_Index}}</div><div class="inline_block">
        </div><div class="inline_block" style="margin: 0 10px">/</div><div class="total_Page">{{total}}</div>
      </div>
      <div class="question">
        <Question />
      </div>
      <div class="btns">
        <div class="btn"><button @click="clickBtn(1)">네</button></div>
        <div class="btn"><button @click="clickBtn(0)">아니오</button></div>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
import Question from '@/components/Question.vue';
import { Next_Question, Go_Start } from '@/store';
export default {
  name: 'Check',
  components: {
    Question
  },
  computed: {
    cur_Question_Index() {
      return (String(this.$store.state.cur_question_index + 1)).padStart(2, "0");
    },
    total() {
      return this.$store.state.question.length;
    },
  },
  methods: {
    clickBtn(answer) {
      this.$store.commit(Next_Question, answer);
    },
    logo() {
      this.$store.commit(Go_Start);
    }
  }
}
</script>

<style scoped>
  button {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }
  .center {
    margin: 150px auto 0;
    width: 370px;
    text-align: center;
  }

  .page {
    text-align: left;
  }

  .cur_Page {
    font-size: 20px;
    font-weight: bold;
    display: inline-block;
  }

  .total_Page {
    font-size: 20px;
    font-weight: 400;
    color: #707070;
    display: inline-block;
  }

  .question {
    margin-top: 23px;
    width: 379px;
    height: 90px;
    text-align: left;
    font-size: 30px;
    font-weight: 500;
  }

  .btns {
    margin-top: 180px;
    margin-left: -30px;
  }

  .btn > button {
    border: none;
