    cursor: pointer;
    width: 180px;
    height: 61px;
    border: 3px solid #A7A7A7;
    border-radius: 31px;
    cursor: pointer;
    
    float: left;
    margin-left: 20px;
    transition: all 0.2s;
  }

  .btn > button:hover {
    border: 3px solid #0A59FA;
