  .btn > button:active {
    background-color: #0A59FA;
    border: 3px solid #0A59FA;
    color: white;
  }

  @media screen and (max-width: 728px) { 
    .center {
      margin: 50px auto 0;
      width: 62%;
      text-align: center;
    }

    .cur_Page {
