<template>
  <div>
    <div class="alert_copy"><span class="alert_copy_text">{{clipboard_Text}}</span></div>
    <div id="Start" class="center"><Start /></div>
    <div id="Check" class="center"><Check /></div>
    <div id="Loading" class="center"><Loading /></div>
    <div id="Result"><Result /></div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import { Go_Start, Page_Type, getSelector, Params_Result}from '@/store';
import Start from '@/components/Start.vue';
import Check from '@/components/Check.vue';
import Loading from '@/components/Loading.vue';
import Result from '@/components/Result.vue';

export default {
  name: '5060Test',
  components: {
    Start,
    Check,
    Loading,
    Result
  },
  computed: {
     ...mapState([
      'clipboard_Text'
    ]),
    Page_Type: Page_Type,
  },
  methods: {
    logo() {
      this.$store.commit(Go_Start);
    }
  }
  ,
  mounted() {
    getSelector();
    if(this.$route.query.result) {
      this.$store.commit(Params_Result, this.$route.query.result);
    }
  },
}
</script>

<style>
.alert_copy {
    position: fixed;
    background-color: #36C98E;
    height: 50px;
    text-align: center;
    top: 0;
    width: 100vw;
    z-index: 1;
    opacity: 0;
    transform: scale(0.5);
    transition: 0.2s;
  }

  .alert_copy_text {
    color: white;
    font-size: 15px;
    margin: 0 auto;
    line-height: 50px;
  }

  .logo {
    margin: 24px auto 0;
    width: 62%;
    text-align: left;
    font-size: 24px;
    font-weight: 900;
    cursor: pointer;
  }

  #Start {
    position: fixed;
    margin: 0 auto;
    left: 0;
    right: 0;
    height: 100%;
    overflow: auto;
    transition: 0.5s;

    /* transform: scale(0);
    opacity: 0; */
  }
  #Check {
    position: fixed;
    margin: 0 auto;
    left: 0;
    right: 0;
    transition: 0.5s;
    transform: scale(0);
    opacity: 0;
  }
  #Loading {
    position: fixed;
    margin: 0 auto;
    left: 0;
    right: 0;
    transition: 0.5s;
    width: 100vw;
    height: 100vh;
    transform: scale(0);
    opacity: 0;
  }
  #Result {
    margin: 0 auto;
    left: 0;
    right: 0;
    display: none;
    transition: 0.5s;
    transform: scale(0);
    opacity: 0;
  }
   @media screen and (max-width: 728px) { 
   
    .logo {
      width: 75%;
      font-size: 15px;
    }
   }

</style>